<template>
<div class="app-page auto-distribution-list">
    <page-content class="page-content">
      <scroller style=""
                :on-refresh="refresh"
                ref="myscroller">
        <div class="list">
        <div class="item" v-for="(item, index) in list" :key="index" @click="toDetail">
          <div class="item-info">
            <div class="info-img">
              <img src="static/images/category/jz.png"/>
            </div>
            <div class="info-detail">
              <div class="info-detail-title">橙子橙子天然维C超甜橙 子橙子天然维C超甜</div>
              <div class="info-detail-specification">规格:&nbsp;3kg</div>
            </div>
            <div class="info-num">
              <div class="info-num-price">¥&nbsp;36.00</div>
              <div class="info-num-quantity">x1</div>
            </div>
          </div>
          <div class="item-progress">
            <van-icon class="icon-passed" name="passed" size=".53rem" v-for="(item, index) in 8" :key="index"/>
            <page-line direction="L" borderStyle="dashed" :length="3.46" :dashedSpacing="0.2"></page-line>
          </div>
          <div class="item-date">
            本次配送时间：01-06 星期天
          </div>
        </div>
      </div>
      </scroller>
    </page-content>
</div>

</template>
<script>
import PageContent from '@/components/page/content'
import PageLine from '@/components/page/line'
export default {
  name: 'autoDistributionList',
  components: { PageContent, PageLine },
  data () {
    return {
      list: [1, 1, 1, 1, 1, 1, 1, 1]
    }
  },
  methods: {
    // 跳转详情页面
    toDetail () {
      this.$router.push({ path: '/autodistribution/detail', query: { id: 1 } })
    },
    /** 下拉刷新相关方法****/
    refresh (done) {
      console.log('下拉刷新')
      setTimeout(() => {
        done()
      }, 500)
    }
  },
  created () {
  }
}

</script>

<style rel='stylesheet/scss' lang='scss' scoped>
  @import '@/styles/variables.scss';
  .auto-distribution-list{
    font-family: $font-family-Medium!important;
    .page-content{
      position: relative;
      background-color: #f9f9f9;
      .list{
        width: 100%;
        .item+.item{
          margin-top: .27rem;
        }
        .item{
          background: #fff;
          width: 100%;height: 326px;
          padding: 30px 50px;
          display: flex;flex-direction: column;justify-content: space-between;
          .item-info{
            width: 100%;height: 150px;
            display: flex;align-items: center;justify-content: space-between;
            .info-img{
              width: 150px;height: 100%;
              img{width: 100%;height: 100%;}
            }
            .info-detail{
              flex: 2;height: 100%;
              padding-left: 50px;
              .info-detail-title{
                font-size: 28px;
                font-weight: 600;
              }
              .info-detail-specification{
                margin-top: 10px;
                font-size: 24px;
                color: $black999999;
              }
            }
            .info-num{
              flex: 1;height: 100%;
              .info-num-price{
                display: flex;flex-direction: row-reverse;
                font-weight: 600;
                font-size: 28px;
              }
              .info-num-quantity {
                display: flex;flex-direction: row-reverse;
                margin-top: 10px;
                font-size: 24px;
                color: $black999999;
              }
            }
          }
          .item-progress{
            width: 100%;height: 40px;
            position: relative;
            display: flex;justify-content: space-between;align-items: center;
            >i{
              background-color: $whiteffffff;
              z-index: 20;
              color: $black999999;
            }
            >.line{
              position: absolute;
              width: 100%!important;
              top: 0;bottom: 0;margin: auto;
              z-index: 10;
            }
          }
          .item-date{
            width: 100%;height: 30px;
            font-size: 24px;
            color: $black999999;
          }
        }
      }
    }
  }
</style>



